import axios from 'axios';
import router from "./router";
import {
    Message,
    Loading
} from "element-ui";


let loading;

function startLoading() {
    loading = Loading.service({
        lock: true,
        text: '...',
        background: 'rgba(0,0,0,0,7)'
    });
}

function endLoading() {
    loading.close();
}
